<template>
	<view>
		<view class="date">
			<i class="iconfont" data-method="undo" @tap="edit" >
				<svg t="1668182431175" class="icon" viewBox="0 0 1024 1024" 
				version="1.1" 
				xmlns="http://www.w3.org/2000/svg" 
				p-id="2212" width="25" height="25">
				<path d="M581.056 288.32H232.96l108.352-102.208c15.552-15.744 19.456-31.104 4.16-46.208-16.064-15.872-32.576-15.808-48.64 0l-145.92 144.768c-8.768 8.832-23.488 20.608-22.08 32.448l0.64 4.8-0.64 4.864c-1.344 11.776 6.4 18.24 14.848 26.816l147.648 145.216c16.064 15.808 38.08 20.992 54.144 5.12 15.296-15.104 3.84-38.208-11.328-53.504L233.152 353.6 581.056 352c126.464 0 250.944 111.488 250.944 236.16C832 712.832 707.52 832 581.056 832H246.4c-22.592 0-29.696 9.6-29.696 32.256s7.04 31.744 29.696 31.744H581.12C755.136 896 896 757.696 896 588.16c0-169.408-140.8-299.84-314.944-299.84z" fill="#bfbfbf" p-id="2213">
				</path>
				<path d="M323.392 192a32 32 0 1 1 0-64 32 32 0 0 1 0 64zM320.192 514.048a32 32 0 1 1 0-64 32 32 0 0 1 0 64zM237.824 896a32 32 0 1 1 0-64 32 32 0 0 1 0 64z" fill="#bfbfbf" p-id="2214"></path></svg>
			</i>
		<i class="iconfont" data-method="redo" @tap="edit" >
			<svg t="1668182476996" class="icon" viewBox="0 0 1024 1024" 
			version="1.1" 
			xmlns="http://www.w3.org/2000/svg" 
			p-id="2703" width="25" height="25">
			<path d="M442.944 288.32h348.032l-108.352-102.208c-15.552-15.744-19.456-31.104-4.16-46.208 16.064-15.872 32.576-15.808 48.64 0l145.92 144.768c8.768 8.832 23.488 20.608 22.08 32.448l-0.64 4.8 0.64 4.864c1.344 11.776-6.4 18.24-14.848 26.816l-147.648 145.216c-16.064 15.808-38.08 20.992-54.144 5.12-15.296-15.104-3.84-38.208 11.328-53.504l101.056-96.832L442.944 352C316.48 352.064 192 463.552 192 588.224 192 712.832 316.48 832 442.944 832h334.592c22.592 0 29.696 9.6 29.696 32.256s-7.04 31.744-29.696 31.744H442.88C268.864 896 128 757.696 128 588.16c0-169.408 140.8-299.84 314.944-299.84z" fill="#bfbfbf" p-id="2704"></path>
			<path d="M700.608 192a32 32 0 1 0 0-64 32 32 0 0 0 0 64zM703.808 514.048a32 32 0 1 0 0-64 32 32 0 0 0 0 64zM786.176 896a32 32 0 1 0 0-64 32 32 0 0 0 0 64z" fill="#bfbfbf" p-id="2705"></path></svg>
			</i>
		<i class="iconfont " @tap="save" >
			<svg t="1668181123445" class="icon" viewBox="0 0 1024 1024" 
			version="1.1" 
			xmlns="http://www.w3.org/2000/svg" 
			p-id="15012" width="26" height="26">
			<path d="M912 190h-69.9c-9.8 0-19.1 4.5-25.1 12.2L404.7 724.5 207 474c-6.1-7.7-15.3-12.2-25.1-12.2H112c-6.7 0-10.4 7.7-6.3 12.9l273.9 347c12.8 16.2 37.4 16.2 50.3 0l488.4-618.9c4.1-5.1 0.4-12.8-6.3-12.8z" p-id="15013"></path></svg>
			</i>
		</view>
		
		<view class="editor_toolbox" v-show="i_show">
			<i class="iconfont icon-img" data-method="insertImg" @tap="edit" />
			<i class="iconfont icon-video" data-method="insertVideo" @tap="edit" />
			<i class="iconfont icon-link" data-method="insertLink" @tap="edit" />
			<i class="iconfont icon-text" data-method="insertText" @tap="edit" />
			<i class="iconfont icon-clear" @tap="clear" />
			
		</view>
		<view style="padding-top: 80rpx;" @click="da">
			<u--textarea 
			v-model="value1"
			focus
			 autoHeight
			 border="bottom"
			 confirmType="done"
			placeholder="请输入标题" ></u--textarea>
			<!-- 小标题 -->
			<u--input
			class="inputXi"
			    placeholder="请输入副标题"
			    border="none"
			    v-model="value"
				prefixIcon="pushpin"
			  ></u--input>

			<mp-html ref="article" container-style="padding:20px" :content="content"
				domain="https://mp-html.oss-cn-hangzhou.aliyuncs.com" :editable="editable"
				placeholder='开始书写'
				 @remove="remove" />
		</view>
		<block v-if="modal">
			<view class="mask" />
			<view class="modal">
				<view class="modal_title">{{modal.title}}</view>
				<input class="modal_input" :value="modal.value" maxlength="-1" auto-focus @input="modalInput" />
				<view class="modal_foot">
					<view class="modal_button" @tap="modalCancel">取消</view>
					<view class="modal_button" style="color:#576b95;border-left:1px solid rgba(0,0,0,.1)"
						@tap="modalConfirm">确定</view>
				</view>
			</view>
		</block>
		<!-- 提示 -->
		<u-toast class="toast" ref="uToast"></u-toast>
	</view>
	
</template>

<script>
	import mpHtml from '@/components/mp-html/mp-html'
	const db=uniCloud.database()
	// 上传图片方法
	function upload(src, type) {
		return new Promise((resolve, reject) => {
			console.log('上传', type === 'img' ? '图片' : '视频', '：', src)
			// resolve(src)
			uniCloud.uploadFile({
				filePath:src.path,
				cloudPath:src.name
			}).then(
			(res)=>{
				console.log(res);
				resolve(res.fileID)
			})
		})
	}
	// 删除图片方法
	function remove(src) {
		console.log('删除图片：', src)
		// 实际使用时，删除线上资源
	}
	export default {
		data() {
			return {
				value1:'',
				value:'',
				content:'',
				modal: null,
				editable: true,
				// 判断底部状态栏
				i_show:false,
				// 富文本类容
				editContent:'',
				// 封面大图
				picurls:'',
				// 判断修改还是增加的判断id
				id:'',
			}
		},
		components: {
			mpHtml
		},
		onLoad(e) {
			this.getDataXiang(e.id);
			this.id=e.id;
		},
		onReady() {
			/**
			 * @description 设置获取链接的方法
			 * @param {String} type 链接的类型（img/video/audio/link）
			 * @param {String} value 修改链接时，这里会传入旧值
			 * @returns {Promise} 返回线上地址
			 *   type 为音视频时可以返回一个数组作为源地址
			 *   type 为 audio 时，可以返回一个 object，包含 src、name、author、poster 等字段
			 */
			this.$refs.article.getSrc = (type, value) => {
				return new Promise((resolve, reject) => {
					if (type === 'img' || type === 'video') {
						uni.showActionSheet({
							itemList: ['本地选取'],
							success: res => {
								if (res.tapIndex === 0) {
									// 本地选取
									if (type === 'img') {
										uni.chooseImage({
											count: value === undefined ? 9 : 1, // 2.2.0 版本起插入图片时支持多张（修改图片链接时仅限一张）
											success: res => {
												console.log('awq',res);
												// #ifdef MP-WEIXIN
												if (res.tempFilePaths.length == 1 && wx.editImage) {
													// 单张图片时进行编辑
													wx.editImage({
														src: res.tempFilePaths[0],
														complete: res2 => {
															uni.showLoading({
																title: '上传中'
															})
															upload(res2.tempFilePath || res.tempFilePaths[0], type).then(res => {
																uni.hideLoading()
																resolve(res)
															})
														}
													})
												} else {
													// #endif
													uni.showLoading({
														title: '上传中'
													});
													(async () => {
														const arr = []
														for (let item of res.tempFiles) {
															// 依次上传
															const src = await upload(item, type)
															arr.push(src)
														}
														return arr
													})().then(res => {
														uni.hideLoading()
														resolve(res)
													})
													// #ifdef MP-WEIXIN
												}
												// #endif
											},
											fail: reject
										})
									} else {
										uni.chooseVideo({
											success: res => {
												let tempFile={name:res.name,path:res.tempFilePath}
												console.log('asd',res);
												uni.showLoading({
													title: '上传中'
												})
												upload(tempFile, type).then(res => {
													uni.hideLoading()
													resolve(res)
												})
											},
											fail: reject
										})
									}
								} else {
									// 远程链接
									this.callback = {
										resolve,
										reject
									}
									this.$set(this, 'modal', {
										title: (type === 'img' ? '图片' : '视频') + '链接',
										value
									})
								}
							}
						})
					} else {
						this.callback = {
							resolve,
							reject
						}
						let title
						if (type === 'audio') {
							title = '音频链接'
						} else if (type === 'link') {
							title = '链接地址'
						}
						this.$set(this, 'modal', {
							title,
							value
						})
					}
				})
			}
		},
		methods: {
			// 查询对应数据
			getDataXiang(id){
				db.collection("note").doc(id).get().then(
				(res)=>{
					this.value1=res.result.data[0].title;
					this.value=res.result.data[0].subhead;
					this.content=res.result.data[0].content;
				})
			},
			//打开底部状态栏
			async da(){
				await db.collection("note").doc(this.id).get().then(
				(res)=>{
					this.value1=res.result.data[0].title;
					this.value=res.result.data[0].subhead;
					this.content=res.result.data[0].content;
					this.i_show=true
					this.editable=true
				})
				
			},
			 btn(){
				setTimeout(()=>{
					var ctx = this.$refs.article
				 var cover = ctx.imgList;
				 this.picurls=cover[0]
				 console.log( this.picurls,typeof(this.picurls));
				 
				 let data={
					 title:this.value1,
					 subhead:this.value,
					 content:this.$refs.article.getContent(),
					 picurls:this.picurls,
					 posttime:Date.now()
				 }
				 if(this.id){
					 db.collection("note").doc(this.id).update({
						 ...data
					 }).then(
					 (res)=>{
						 // console.log(res);
						 uni.showToast({
						 	title:'保存成功'
						 })
					 })
					 .catch((err)=>{
						 uni.showToast({
						 	title:'保存失败',
							icon:'error'
						 })
					 })
				 }else{
					 db.collection("note").add({
					 ...data
				 }).then(
				 (res)=>{
					 // console.log(res);
					 uni.showToast({
					 	title:'保存成功'
					 })
				 })
				 .catch(
				 (err)=>{
					 console.log(err);
					 this.$refs.uToast.show(
					 		{
					 		type: 'error',
							duration:1500,
					 		icon:'close-circle-fill',
					 		message:err+'',
					 				})
				 })
				 }
				 
				 
				},100)
				 
				 
				 // this.editable=true
			},
			// 删除图片/视频/音频标签事件
			remove(e) {
				// 删除线上资源
				remove(e.src)
			},
			// 处理模态框
			modalInput(e) {
				this.value = e.detail.value
			},
			modalConfirm() {
				this.callback.resolve(this.value || this.modal.value || '')
				this.$set(this, 'modal', null)
			},
			modalCancel() {
				this.callback.reject()
				this.$set(this, 'modal', null)
			},
			// 调用编辑器接口
			edit(e) {
				this.$refs.article[e.currentTarget.dataset.method]()
			},
			// 清空编辑器内容
			clear() {
				uni.showModal({
					title: '确认',
					content: '确定清空内容吗？',
					success: res => {
						if (res.confirm)
							this.$refs.article.clear()
							this.value1=''
					}
				})
			},
			// 保存编辑器内容
			save() {
				setTimeout(() => {
					var content = this.$refs.article.getContent()
					uni.showModal({
						title: '保存',
						content:'保存以上数据',
						confirmText: '完成',
						success: res => {
							if (res.confirm) {
								this.btn();
								this.editContent=this.$refs.article.getContent()
								this.editable=false
								this.i_show = false
							}
						}
					})
				}, 50)
			}
		}
	}
</script>

<style scoped>
	/* 小标题 */
	.inputXi{
		margin-top:20rpx
	}
	/* 提示样式 */
/deep/.u-icon__icon{
		font-size: 20px !important;
	}
	/* 去除标题底色 */
	.u-textarea{
		background-color: transparent;
		font-weight: 700;
	}
	.uni-textarea-compute, .uni-textarea-line,
	 .uni-textarea-placeholder, .uni-textarea-textarea{
		font-size:16px;
		line-height: 16px;
	}
	>>>.uni-textarea-textarea{
			color: #000;
			
		}
		/* 保存顶部样式 */
		.date{
			position: absolute;
			right: 0;
		}
		.date i{
			padding-right: 30rpx;
		}
	.editor_toolbox {
		position: fixed;
		bottom: 0;
		width: 100%;
		z-index: 999;
		background-color: #f6f6f8;
		border-bottom: 30rpx solid #f6f6f8;
		display: flex;
		padding: 5px;
		box-sizing: border-box;
		line-height: 1.6;
	}

	@font-face {
		font-family: "iconfont";
		src: url('data:application/x-font-woff2;charset=utf-8;base64,') format('woff2');
	}

	.iconfont {
		flex: 1;
		text-align: center;
		font-family: "iconfont" !important;
		font-size: 22px;
		font-style: normal;
		-webkit-font-smoothing: antialiased;
		-moz-osx-font-smoothing: grayscale;
	}
	.iconfont:active{
		color: red;
	}

	.icon-undo:before {
		content: "\e607";
	}

	.icon-redo:before {
		content: "\e606";
	}

	.icon-img:before {
		content: "\e6e2";
	}

	.icon-video:before {
		content: "\e798";
	}

	.icon-link:before {
		content: "\e60d";
	}

	.icon-text:before {
		content: "\e6ce";
	}

	.icon-clear:before {
		content: "\e637";
	}

	.icon-save:before {
		content: "\e501";
	}

	/* 模态框 */
	.modal {
		position: fixed;
		top: 50%;
		left: 16px;
		right: 16px;
		background-color: #fff;
		border-radius: 12px;
		transform: translateY(-50%);
	}

	.modal_title {
		padding: 32px 24px 16px;
		font-size: 17px;
		font-weight: 700;
		text-align: center;
	}

	.modal_input {
		display: block;
		padding: 5px;
		margin: 0 24px 32px 24px;
		font-size: 14px;
		border: 1px solid #dfe2e5;
	}

	.modal_foot {
		display: flex;
		line-height: 56px;
		font-weight: 700;
		border-top: 1px solid rgba(0, 0, 0, .1);
	}

	.modal_button {
		flex: 1;
		text-align: center;
	}

	/* 蒙版 */
	.mask {
		position: fixed;
		top: 0;
		right: 0;
		bottom: 0;
		left: 0;
		background-color: black;
		opacity: 0.5;
	}
</style>
